@import './src/assets/scss/variables';

.usersContainer {
  background-color: $main-color;

  .usersNumbersPages {
    height: 40px;

    span {
      padding: 3px;
      border: 1px solid #FDFDFF;
    }
  }

}

.hz{
  background-color: #393D3F;
}
.usersWithServer{
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  .userWithServer{
    width: 300px;
    border: 5px solid $fourth-color;
    border-radius: 10px;
    padding: 5px;
    margin: 10px;
  }
}
.photo {
  width: 80px;
  height: 60px;
}

.selectedPage {
  font-weight: bold;
}